/* HamburgerMenu color palette */
/* HamburgerMenu main styling sets */
body .navigation-container .nav-wrapper #hamburger-menu-wrapper {
  display: none;
  height: 26px;
  position: relative;
  z-index: 120;
  transition: all 350ms ease;
}

body .navigation-container .nav-wrapper #hamburger-menu-wrapper:hover div {
  background: #e91e63;
}

body .navigation-container .nav-wrapper #hamburger-menu-wrapper div {
  width: 25px;
  height: 1px;
  opacity: 1;
  position: relative;
  background: #FFFFFF;
  transform-origin: 1px;
  border-radius: 5px;
  transition: all 0.4s ease;
}

body .navigation-container .nav-wrapper #hamburger-menu-wrapper div:nth-child(2) {
  width: 12.5px;
}

body .navigation-container .nav-wrapper #hamburger-menu-wrapper div.opened:first-child {
  transform: rotate(45deg);
  background: #e91e63;
  transition: all 0.4s ease;
}

body .navigation-container .nav-wrapper #hamburger-menu-wrapper div.opened:nth-child(2) {
  opacity: 0;
  transform: rotate(360deg);
  background: #e91e63;
  transition: all 0.1s ease;
}

body .navigation-container .nav-wrapper #hamburger-menu-wrapper div.opened:nth-child(3) {
  opacity: 1;
  transform: rotate(-45deg);
  background: #e91e63;
  transition: all 0.4s ease;
}

body .navigation-container .nav-wrapper #hamburger-menu-wrapper div.closed:nth-child(2) {
  opacity: 1;
  transform: translateX(12.5px);
  transition: all 0.4s linear;
}

body .navigation-container .nav-wrapper #hamburger-menu-wrapper div.closed:nth-child(3) {
  opacity: 0;
}

body .navigation-container .nav-wrapper #hamburger-menu-wrapper:focus {
  outline: none;
}

@media only screen and (max-width: 576px) {
  body .navigation-container .nav-wrapper {
    justify-content: flex-end !important;
    align-items: center;
  }
  body .navigation-container .nav-wrapper #hamburger-menu-wrapper {
    display: flex;
  }
}
/*# sourceMappingURL=hamburgerMenu.css.map */
