/* NavigationBar color palette */
/* NavigationBar main styling sets */
body .navigation-container {
  min-height: 8vh;
  position: fixed;
  transform: translateY(0vh);
  transition: all 0.35s cubic-bezier(0.65, 0, 0.4, 0.5);
  z-index: 100;
  background: #141414;
  -webkit-backdrop-filter: saturate(200%) blur(20px);
          backdrop-filter: saturate(200%) blur(20px);
}

body .navigation-container.hidden {
  transform: translateY(-8vh);
  transition: all 0.35s cubic-bezier(0.6, 0.5, 0.35, 1);
}

body .navigation-container.shown {
  box-shadow: 0.2rem 0.5rem 0.3rem rgba(0, 0, 0, 0.2);
  background: rgba(20, 20, 20, 0.7);
  transform: translateY(0vh);
  transition: all 0.35s cubic-bezier(0.65, 0, 0.4, 0.5);
}

body .navigation-container .nav-wrapper {
  display: flex;
  min-height: 8vh;
  justify-content: flex-end;
}

/* MEDIA QUERIES */
@media only screen and (max-width: 768px) {
  body .navigation-container .nav-wrapper {
    justify-content: center;
  }
}

@media only screen and (max-width: 576px) {
  body .navigation-container .nav-wrapper nav {
    display: none;
  }
}
